<template>
  <div>
    <el-form :inline="true" :model="params" class="demo-form-inline">
      <el-form-item label="财务组织：" size="mini">
        {{company_name}}
      </el-form-item>
      <el-form-item label="业务日期" size="mini">
        <el-date-picker v-model="timeValue" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="供应商(厂家):" size="mini">
        {{supplier_name}}
      </el-form-item>
      <el-form-item size="mini" class="search-style search">
        <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
        <el-button plain @click="onReset" size="mini">重置</el-button>
      </el-form-item>
    </el-form>
    <div class="table_container">
      <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" max-height="600" style="width: 100%">
        <el-table-column :show-overflow-tooltip="true" property="business_time" label="业务日期"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="advance_price" label="预付">
          <template slot-scope="scope">
            <pre>{{scope.row.advance_price | priceFormatNone}}</pre>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="use_advance_price" label="使用预付">
          <template slot-scope="scope">
            <pre>{{scope.row.use_advance_price | priceFormatNone}}</pre>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="deposits" label="余额">
          <template slot-scope="scope">
            <pre>{{scope.row.deposits | priceFormatNone}}</pre>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="reference_sn" label="关联单据"></el-table-column>
      </el-table>
    </div>
    <div class="page_container">
      <div class="pageNumber">每页显示</div>
      <el-pagination class="erp-pagination" @size-change="handleSizeChange" :current-page.sync="goPage" :page-size="pageSize" @current-change="handleCurrentChange" :page-sizes="per_page" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="TableData.total">
      </el-pagination>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="goBack">返 回</el-button>
    </div>
  </div>
</template>

<script>
import { getData } from "common/utils/public_function/commonUtils";
export default {
  name: "purchaseFactoryprepaidsearch",
  data() {
    return {
      formInlineParams: {},
      timeValue: "",
      TableData: {
        tableData: []
      },
      company_name: "",
      supplier_name: ""
    };
  },
  methods: {
    onSearch() {
      this.company_name = this.$route.query.company_name;
      this.supplier_name = this.$route.query.supplier_name;
      this.$axios({
        method: "get",
        url: this.$api.getPrepaidDetailList,
        params: {
          start_time: this.timeValue[0],
          end_time: this.timeValue[1],
          goPage: this.goPage,
          pageSize: this.pageSize,
          supplier_id: this.$route.query.supplier_id,
          company_id: this.$route.query.company_id
        }
      }).then(({ data }) => {
        this.TableData.tableData = data.data.data;
      });
    },
    handleSizeChange(val) {
      Object.assign(this.formInlineParams.params, {
        per_page: val,
        page: 1
      });
      getData(this.formInlineParams);
    },
    handleCurrentChange(val) {
      Object.assign(this.formInlineParams.params, {
        page: val
      });
      getData(this.formInlineParams);
    },
    onReset() {
      this.$set(this, "params", {});
      this.timeValue = [];
      this.$set(this, "formInlineParams", {
        params: {
          supplier_id: this.$route.query.supplier_id,
          company_id: this.$route.query.company_id
        },
        TableData: this.TableData, //列表数据
        API: "getPrepaidDetailList", //接口名
        vm: this //传递的this
      });
      getData(this.formInlineParams);
    },
    goBack() {
      this.$router.push({ path: "/purchase/factoryprepaid" });
    }
  },
  created() {
    if (this.$route.query.supplier_id != undefined) {
      this.company_name = this.$route.query.company_name;
      this.supplier_name = this.$route.query.supplier_name;
      this.$set(this, "formInlineParams", {
        params: {
          goPage: 1,
          pageSize: 30,
          supplier_id: this.$route.query.supplier_id,
          company_id: this.$route.query.company_id
        }, //1默认带的参数
        TableData: this.TableData, //列表数据
        API: "getPrepaidDetailList", //接口名
        vm: this //传递的this
      });
      getData(this.formInlineParams);
    }
  }
};
</script>

<style scoped>
.el-form-item--mini.el-form-item {
  width: 350px;
}
</style>
